
'use client';

import { useEffect, useState } from 'react';
import { doc, getDoc } from 'firebase/firestore';
import { db } from '@/firebase/firebase';

export default function useWebsiteSettings() {
  const [settings, setSettings] = useState(null);

  useEffect(() => {
    async function fetchSettings() {
      try {
        const docRef = doc(db, 'settings', 'metadata'); // ← tuỳ theo bạn lưu document này thế nào
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
          const data = docSnap.data();
          setSettings(data);

          // Optional: Cache vào localStorage nếu cần
          localStorage.setItem('websiteSettings', JSON.stringify(data));
        }
      } catch (error) {
        console.error("Không lấy được metadata từ Firestore:", error);
      }
    }

    fetchSettings();
  }, []);

  return settings;
}
